import React, { useCallback, useEffect, useRef, useState } from "react";
import { Button, FormGroup } from "mtforms";
import { useIsMutating } from "@tanstack/react-query";

import { useClockIN, useFirstClockIN, useUserDetails } from "./hooks";
import { PictureUpload } from "../../Register/components/PictureUpload";
import { EmailAddress } from "./components/EmailAddress";
import { useMultistepForm } from "../../../../utils/useMultistepForm";
import { dataURLtoFile, infoAlert, successAlert } from "../../../../utils";

const ClockIn = () => {
  const [formData, setFormData] = useState({});
  const [picture, setPicture] = useState(null);
  const [errors, setErrors] = useState({});
  const [cameraActive, setCameraActive] = useState(false);
  const [first, setFirst] = useState(false);
  const webcamRef = useRef(null);
  const { mutate, isSuccess, reset } = useClockIN();
  const {
    mutate: firstClockin,
    isSuccess: isFSuccess,
    reset: fReset,
  } = useFirstClockIN();
  const isLoading = useIsMutating();

  const userInfo = useUserDetails(formData["email"]);
  console.log(userInfo, "userInfo");
  // STUB: for location services
  useEffect(() => {
    const getLocation = () => {
      if (!navigator.geolocation) {
        setErrors({
          ...errors,
          location: "Geolocation is not supported by your browser",
        });
      } else {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            setFormData({
              ...formData,
              long: position.coords.longitude.toString(),
              lat: position.coords.latitude.toString(),
            });
          },
          () => {
            setErrors({
              ...errors,
              location: "Unable to retrieve your location",
            });
          }
        );
      }
    };
    getLocation();
  }, []);

  // STUB: capture image & deactivate camera
  const capture = useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    if (imageSrc) {
      setPicture(imageSrc);
      setFormData({
        ...formData,
        file: dataURLtoFile(imageSrc, "image"),
      });
      setCameraActive(false);
      successAlert(`Picture captured!`);
    } else {
      infoAlert(`Please wait for camera to load before clicking on capture!`);
    }
  }, [webcamRef, formData]);

  // STUB: turn on camera
  const handleCameraClick = (e) => {
    setCameraActive(true);
  };

  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };

  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  // STUB: multistep form hook
  const { steps, currentStepIndex, step, isFirstStep, isLastStep, back, next } =
    useMultistepForm([
      <EmailAddress
        formData={formData}
        errors={errors}
        validationHandler={validationHandler}
        handleChange={handleChange}
      />,
      <PictureUpload
        webcamRef={webcamRef}
        isLoading={isLoading}
        capture={capture}
        cameraActive={cameraActive}
        handleCameraClick={handleCameraClick}
        picture={picture}
        formData={formData}
        first={first}
        data={userInfo}
      />,
    ]);

  const submitHandler = () => {
    if (!isLastStep) {
      if (
        userInfo?.face_encoding === null ||
        userInfo?.face_encoding === undefined
      ) {
        setFormData({ ...formData, id: userInfo.id });
        setFirst(true);
      }
      return next();
    }
    if (!formData.file) {
      infoAlert(
        `No picture found.
				Please click on 'Take Picture' to attach your picture to the form`
      );
      return;
    }
    if (first) {
      firstClockin(formData);
    } else {
      mutate(formData);
    }
  };

  if (isSuccess) {
    reset();
  }

  return (
    <>
      <h1>Clock In</h1>
      <FormGroup onSubmit={submitHandler}>
        <div
          style={{
            position: "absolute",
            top: ".5rem",
            right: ".5rem",
          }}
        >
          {currentStepIndex + 1} / {steps.length}
        </div>
        {step}
        <div
          style={{
            marginTop: "1rem",
            display: "flex",
            flexDirection: "column",
            gap: ".5rem",
          }}
        >
          <Button
            title={isLastStep ? "Finish" : "Next"}
            size="large"
            type="submit"
            className={isLoading ? "btn-disabled btnPurple" : "btnPurple"}
            loading={isLoading === 1}
            disabled={isLoading === 1}
          />
          {!isFirstStep && (
            <Button
              title="back"
              size="large"
              onClick={back}
              type="button"
              // style={{ background: "black" }}
              className={isLoading ? "btn-disabled" : "btnRedTxt"}
            />
          )}
        </div>
      </FormGroup>
    </>
  );
};

export default ClockIn;
