import { Input } from "mtforms";
import { FormWrapper } from "../../../Register/components/FormWrapper";

export function EmailAddress({
	formData,
	handleChange,
	validationHandler,
	errors,
}) {
	return (
		<FormWrapper title="Enter details">
			<Input
				name="email"
				label="Email Address"
				value={formData["email"]}
				onChange={handleChange}
				type="email"
				validationHandler={validationHandler}
				error={errors.email}
				required={true}
				size="large"
			/>
		</FormWrapper>
	);
}
