import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { axiosInstance } from "../../../../../axios-Instance";
import { queryKeys } from "../../../../../react-query/constants";
import { PublicPaths } from "../../../../../routes/path";
import { errorAlert, toastOptions } from "../../../../../utils";

async function userClockin(formData) {
  const data = await axiosInstance({
    url: `/attendance/sign-in?email=${formData["email"]}&long=${formData.long}&lat=${formData.lat}`,
    method: "POST",
    data: { file: formData.file },
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });

  return data?.data;
}

async function userFirstClockin(formData) {
  try {
    // First API call
    const register = await axiosInstance({
      url: `/user/${formData["id"]}/image`,
      method: "PATCH",
      data: { file: formData.file },
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });

    // Check if the first API call was successful
    if (register && register.status === 200) {
      // Second API call
      const data = await axiosInstance({
        url: `/attendance/sign-in?email=${formData["email"]}&long=${formData.long}&lat=${formData.lat}`,
        method: "POST",
        data: { file: formData.file },
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });

      // Return data from the second API call
      return data?.data;
    } else {
      // Handle the case where the first API call was not successful
      console.error("First API call failed");
      return {
        success: false,
        error: "An error Occured, Please try again",
      }; // or throw an error, depending on your requirements
    }
  } catch (error) {
    // Handle errors from both API calls
    console.error("Error:", error.message);
    throw error; // or throw an error, depending on your requirements
  }
}

async function userDetails(formData) {
  const data = await axiosInstance({
    url: `/user/email/${formData}`,
    method: "GET",

    headers: {
      "Content-Type": "application/json",
    },
  });

  return data?.data?.data;
}
async function userClockOut(formData) {
  const data = await axiosInstance({
    url: `/attendance/sign-out?email=${formData["email"]}&long=${formData.long}&lat=${formData.lat}`,
    method: "PATCH",
    data: { file: formData.file },
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });

  return data?.data;
}

