export function useUserDetails(user) {
  const fallback = [];
  const { data = fallback } = useQuery({
    queryKey: [queryKeys.user, user],
    queryFn: () => userDetails(user),
    onError: (error) => {
      //   toast.error(error, toastOptions);
    },
  });
  return data;
}
