import { Button } from "mtforms";
import Webcam from "react-webcam";

import { FormWrapper } from "./FormWrapper";
import "./picture-upload.css";

export function PictureUpload({
  webcamRef,
  capture,
  cameraActive,
  handleCameraClick,
  picture,
  formData,
  first,
  data,
}) {
  let output;

  if (cameraActive) {
    output = (
      <>
        {first && (
          <div>
            <h3>Hi {data?.first_name}</h3>
            <p>
              Please take a picture to setup your Face Recognition for the first
              time
            </p>
          </div>
        )}
        <div className="webcam">
          <Webcam audio={false} ref={webcamRef} screenshotFormat="image/jpeg" />
        </div>
        <Button
          title="capture"
          type="button"
          style={{ background: "black" }}
          onClick={capture}
        />
      </>
    );
  }
  if (!cameraActive && picture) {
    output = (
      <>
        <div className="webcam">
          <img src={picture} alt={formData.first_name} />
        </div>
        <Button
          style={{ background: "black" }}
          title="retake picture"
          type="button"
          onClick={handleCameraClick}
        />
      </>
    );
  }
  if (!cameraActive && !picture) {
    output = (
      <Button
        style={{ background: "#3c7d5c" }}
        title="take picture"
        type="button"
        onClick={handleCameraClick}
      />
    );
  }

  return <FormWrapper title="Picture Upload">{output}</FormWrapper>;
}
